import React from 'react';
import Title from './Title';

export default function Header({
  title,
  backPressed,
}: {
  title: string;
  backPressed?: () => void;
}): JSX.Element {
  return (
    <div className="header">
      {backPressed && (
        <div className="header-back" onClick={backPressed}>
          &lt;
        </div>
      )}
      <Title title={title} />
    </div>
  );
}
